/* Simple Editor Layout */
.simple-markup-editor,
.simple-annotation-editor {
    background: #f8f9fa;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 16px;
}

.simple-markup-editor h3,
.simple-annotation-editor h3 {
    margin: 0 0 12px 0;
    font-size: 16px;
    color: #333;
    font-weight: 600;
}

.simple-controls {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

/* Text Markup Styles */
.btn-select {
    padding: 8px 12px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: background 0.2s;
}

.btn-select:hover {
    background: #0056b3;
}

.selected-text {
    padding: 8px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    color: #666;
    min-height: 20px;
}

.simple-settings {
    display: flex;
    gap: 8px;
    align-items: center;
}

.input-group {
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: flex-start;
}

.input-group label {
    font-size: 12px;
    color: #666;
    font-weight: 500;
    margin: 0;
}

.color-input {
    width: 30px;
    height: 30px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.page-input {
    padding: 6px 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    width: 80px;
}

.markup-buttons {
    display: flex;
    gap: 6px;
}

.btn-markup {
    padding: 6px 12px;
    border: none;
    border-radius: 4px;
    font-size: 12px;
    cursor: pointer;
    transition: opacity 0.2s;
}

.btn-markup.highlight {
    background: #ffd700;
    color: #333;
}

.btn-markup.underline {
    background: #4facfe;
    color: white;
}

.btn-markup.strikethrough {
    background: #ff6b6b;
    color: white;
}

.btn-markup:hover:not(:disabled) {
    opacity: 0.8;
}

.btn-markup:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Text Annotation Styles */
.type-selector {
    display: flex;
    gap: 4px;
}

.type-selector button {
    padding: 6px 12px;
    border: 1px solid #ddd;
    background: white;
    border-radius: 4px;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.2s;
}

.type-selector button:hover {
    background: #f8f9fa;
}

.type-selector button.active {
    background: #007bff;
    color: white;
    border-color: #007bff;
}

.text-input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    resize: vertical;
    min-height: 40px;
}

.position-controls,
.size-controls,
.color-controls {
    display: flex;
    gap: 6px;
    align-items: flex-end;
    flex-wrap: wrap;
}

.pos-input,
.size-input {
    padding: 6px 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 12px;
    width: 60px;
}

.color-controls .color-input {
    width: 25px;
    height: 25px;
}

.btn-add {
    padding: 8px 16px;
    background: #28a745;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: background 0.2s;
}

.btn-add:hover:not(:disabled) {
    background: #218838;
}

.btn-add:disabled {
    background: #6c757d;
    cursor: not-allowed;
}

/* Status Messages */
.loading,
.error,
.success {
    padding: 8px 12px;
    border-radius: 4px;
    font-size: 14px;
    margin-top: 8px;
}

.loading {
    background: #d1ecf1;
    color: #0c5460;
    border: 1px solid #bee5eb;
}

.error {
    background: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.success {
    background: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
}

/* Beautiful Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    backdrop-filter: blur(4px);
}

.text-selection-modal {
    background: white;
    border-radius: 16px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
    max-width: 500px;
    width: 90%;
    max-height: 80vh;
    overflow: hidden;
    animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
    from {
        opacity: 0;
        transform: translateY(-50px) scale(0.9);
    }
    to {
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}

.modal-header {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 20px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h3 {
    margin: 0;
    font-size: 18px;
    font-weight: 600;
}

.modal-close {
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
    padding: 0;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: background 0.2s;
}

.modal-close:hover {
    background: rgba(255, 255, 255, 0.2);
}

.modal-body {
    padding: 24px;
}

.modal-body p {
    margin: 0 0 8px 0;
    color: #666;
    font-size: 14px;
    line-height: 1.5;
}


.paste-buttons {
    margin: 0 0 16px 0;
    display: flex;
    gap: 8px;
}

.btn-paste {
    padding: 8px 16px;
    background: #f0f8ff;
    border: 1px solid #4a90e2;
    border-radius: 6px;
    color: #1976d2;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.2s;
}

.btn-paste:hover {
    background: #e3f2fd;
    border-color: #1976d2;
}

.btn-clear {
    padding: 8px 16px;
    background: #fff3e0;
    border: 1px solid #ff9800;
    border-radius: 6px;
    color: #f57c00;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.2s;
}

.btn-clear:hover {
    background: #ffe0b2;
    border-color: #f57c00;
}


.modal-textarea {
    width: 100%;
    padding: 16px;
    border: 2px solid #e1e5e9;
    border-radius: 8px;
    font-size: 16px;
    font-family: inherit;
    resize: vertical;
    min-height: 80px;
    transition: border-color 0.3s;
    box-sizing: border-box;
    user-select: text;
    -webkit-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
}

.modal-textarea:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.modal-footer {
    padding: 20px 24px;
    background: #f8f9fa;
    display: flex;
    justify-content: flex-end;
    gap: 12px;
}

.btn-cancel {
    padding: 10px 20px;
    background: #6c757d;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    cursor: pointer;
    transition: background 0.2s;
}

.btn-cancel:hover {
    background: #5a6268;
}

.btn-confirm {
    padding: 10px 20px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
}

.btn-confirm:hover:not(:disabled) {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.btn-confirm:disabled {
    background: #ccc;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
}

/* Responsive Design */
@media (max-width: 768px) {
    .simple-settings,
    .position-controls,
    .size-controls,
    .color-controls {
        flex-wrap: wrap;
    }
    
    .markup-buttons {
        flex-wrap: wrap;
    }
    
    .type-selector {
        flex-wrap: wrap;
    }
    
    .text-selection-modal {
        width: 95%;
        margin: 20px;
    }
    
    .modal-header,
    .modal-body,
    .modal-footer {
        padding: 16px;
    }
    
    .modal-footer {
        flex-direction: column;
    }
    
    .btn-cancel,
    .btn-confirm {
        width: 100%;
    }
}
